import React, { useEffect,useState } from "react";
import { useNavigation } from "../Router/Router";
import Axios from "axios";
import { useNavigate } from 'react-router-dom';
import "./css/RecommandPage.css";
import { useLocation } from 'react-router-dom';
import io from 'socket.io-client';

const RecommandPage = () => {
    const location = useLocation();
    const [price,setPrice] = useState();
    const navigate = useNavigate()
    const [products1, setProducts1] = useState([]);
    const [products2, setProducts2] = useState([]);
    const [products3, setProducts3] = useState([]);
    const { onClickStart, onClickOwner, onClickPay, onClickDone, onClickMain } = useNavigation();
    const [ttsEnabled, setTtsEnabled] = useState(false);
    useEffect(() => {
      const ttsState = sessionStorage.getItem("ttsEnabled");
      if (ttsState) {
        setTtsEnabled(JSON.parse(ttsState));
      }
    }, []);
    const handleMouseEnter = (event) => {
      if (ttsEnabled) {
        const tts_script = event.target.innerText;
        const utterance = new SpeechSynthesisUtterance(tts_script); // SpeechSynthesisUtterance  
        window.speechSynthesis.speak(utterance); // TTS 
      }
    };
      useEffect(() => {
        fetch('/get_random_products')
          .then(response => response.json())
          .then(data => {
            //    
            console.log(data);
            const basePath = data.products[0].image.split("static")[0];
            const imagePath1 = data.products[0].image
 
            
            //          
            const imageUrl1 = imagePath1.replace(basePath, '');
            


            const imagePath2 = data.products[1].image
   
            
            //          
            const imageUrl2 = imagePath2.replace(basePath, '');
            
            const imagePath3 = data.products[2].image
   
            
            //          
            const imageUrl3 = imagePath3.replace(basePath, '');

            setProducts1([data.products[0].name, data.products[0].price, imageUrl1]);
          setProducts2([data.products[1].name, data.products[1].price, imageUrl2]);
          setProducts3([data.products[2].name, data.products[2].price, imageUrl3]);
      
            //   
            console.log(products1);
           // console.log(products2);
           // console.log(products3);
          })
          .catch(error => {
            //  
            console.error('    :', error);
          });
      }, []);


      function getCookie(name) {
        const cookies = document.cookie.split(';');
        for (let i = 0; i < cookies.length; i++) {
            const cookie = cookies[i].trim();
            //    
            if (cookie.startsWith(name + '=')) {
                //   
                return cookie.substring(name.length + 1);
            }
        }
        //     null 
        return null;
    }
    function setCookie(name, value, days) {
      const expirationDate = new Date();
      expirationDate.setDate(expirationDate.getDate() + days);
      const expires = expirationDate.toUTCString();
      document.cookie = `${name}=${value}; expires=${expires}; path=/`;
  }
  
    const onClickpay = () => {
    // url    
    const urlCookie = getCookie('url');
    console.log(urlCookie)
    navigate('/pay?amount='+getCookie('sumprice'));
    }

    const onClickAdd1 = () => {
      const priceCookie = Number(getCookie('sumprice')) + Number(products1[1]);
      setCookie('sumprice',priceCookie,1)
 
    }
    const onClickAdd2 = () => {
      const priceCookie = Number(getCookie('sumprice')) + Number(products2[1]);
      setCookie('sumprice',priceCookie,1)
  }
  const onClickAdd3 = () => {
    const priceCookie = Number(getCookie('sumprice')) + Number(products3[1]);
    setCookie('sumprice',priceCookie,1)

}
    return (
      <div className="wrapper1">
          <header>
            <div className="Header">
              <div className="goods">1. </div>
              <div className="pay">2.  </div>
              <div className="done">3.  </div>
            </div>
          </header>
        <div className="wrapper">
        <div className="main">
            <div className="no">No</div>
            <div className="pic"></div>
            <div className="name"></div>
            <div className="number"></div>
            <div className="price"></div>
          </div>
           
          <div className="recommendBackground">
            <div>
                !   
       
                <div> <span style={{color: '#d24232'}}></span> 
                </div>
            </div>
            <div className="wrapperrec">
                <div className="firstdiv">
                    {products1[0]}
                    <p>{products1[1]}</p>
                    <img src={products1[2]}/>
                    {console.log(products1[2])}
                    <button onClick={onClickAdd1} className="payButton" onMouseEnter={handleMouseEnter}></button>
                </div>
                <div className="seconddiv">
                {products2[0]}
                <p>{products2[1]}</p>
                <img src={products2[2]}/>
                <button onClick={onClickAdd2} className="payButton" onMouseEnter={handleMouseEnter}></button>
                </div>
                <div className="thirddiv">
                    {products3[0]}
                    <p>{products3[1]}</p>
                    <img src={products3[2]}/>
                    <button onClick={onClickAdd3} className="payButton" onMouseEnter={handleMouseEnter}></button>
                </div>
            </div>
                <button onClick={onClickpay} className="payButton" onMouseEnter={handleMouseEnter}></button>
            <div>

            </div>
          </div>
        </div>
      </div>
    );
  };
  
  export default RecommandPage;